.random-bird {
  display: flex;
  padding: 1rem;
  margin-bottom: 25px;
}

.random-bird .bird-image {
  box-sizing: border-box;
  width: 200px;
  height: 155px;
  border-radius: 10px;
  margin-right: 2rem;
  object-fit: cover;
}

.random-bird > div {
  width: 100%;
  margin: 0;
}

.random-bird .list-group {
  width: 100%;
  margin: 0;
}

.random-bird .list-group-item {
  padding: .25rem;
}

@media (max-width: 570px) {  
  .random-bird {
    flex-direction: column;
  }
  
  .random-bird .bird-image {
    margin: 0 auto 25px;
  }
  
  h3 {
    text-align: center;
  }
}